import SaleOrderService from "./../../utils/services/OrderService"

export default {
  name: "sale-order",
  data() {
    return {
      isLoading: false,
      saleOrders: "",
    };
  },
  components: {},
  created() {
    this.getList()
  },
  mounted() {},
  methods: {
    getList(){
      SaleOrderService.getSaleOrders()
      .then((response) => {
        if(response.results){
          this.saleOrders = response.results
        }
      })
    },

    onUpdateComplete(index){
      let id = this.saleOrders[index].id
      SaleOrderService.updateSaleOrder(id).then(response => {
        if(response.response.status == 200){
          this.$toast.success("Update successfully!")
          this.saleOrders.splice(index, 1)
        }
      })
    }
  },
};