import Menusidebar from './../menusidebar'
import { mapState } from 'vuex'
import MainService from '../../utils/services/MainService'
import SaleOrder from './../../views/sale-order'
export default {
    name: 'navbar',
    data() {
        return {
            user: "",
            isModalSaleOrder: false,
            isModalSwitchChannel: false,
            switchChannelId: -1,
            modalChooseCustomer: false,
            customerSelected: "",


            data: {
                customers: []
            },
            selectedCustomer: "",
            currentChannel: {},
            ani: false,
            searchQuery: "",
            customerQuery: "",
            popupshow: {
                profile: false,
                sidebar: false,
            },
            modal: {
                channel: {
                    index: -1,
                    show: false,
                },
                customer: {
                    index: -1,
                    show: false,
                    isNoCustomer: false,
                },
                large: false,
            },
        }
    },
    created(){
        this.user = this.$cookies.get("user")
    },
    components: {
        Menusidebar,
        SaleOrder
    },
    computed: {
        ...mapState(["MainStore"]),
    },

    methods: {
        chooseCustomer(customer) {
            this.customerSelected = customer
        },
        confirmChooseCustomer(){
            let customer = {
                id: this.customerSelected.id,
                name: this.customerSelected.firstName+" "+this.customerSelected.lastName
            }
            this.$store.commit("SET_NEW_CUSTOMER", customer);
            this.modalChooseCustomer = false;
            this.customerSelected = ""
        },
        switchChannel(channelId) {
            this.switchChannelId = channelId;
            this.isModalSwitchChannel = true
        },
        confirmSwitchChannel() {
            this.$router.push({name: 'dashboard', query:{channelId: this.switchChannelId}})
            location.reload()
        },


        // Customer Section
        onAddNewOrder() {
            this.data.customers = this.customers
            this.modal.customer.show = true
        },
        onCloseAddNewOrder() {
            this.modal.customer.show = false
        },
        mapFullName(firstName, lastName) {
            return firstName + " " + lastName;
        },
        onCloseSelectCustomer() {
            this.modal.customer.show = false
        },
        
        customerSearch() {
            this.modal.customer.isNoCustomer = false;
            if (this.customerQuery.trim() && this.customerQuery.trim().length > 0) {
                const customerResults = this.data.customers.filter((customer) => {
                    if (customer.firstName.toLowerCase().includes(this.customerQuery.toLowerCase().trim())) {
                        return customer;
                    }
                })
                if (customerResults.length > 0) { return this.data.customers = customerResults } else {
                    return this.modal.customer.isNoCustomer = true;
                }
            }
            return this.data.customers = this.customers
        },
        isCustomerAlreadySelect(customerId) {
            var _count = 0;
            this.orderCustomer.forEach(element => {
                if (element.id == customerId) {
                    _count++;
                }
            });
            return _count == 1 ? true : false;
        },
        onConfirmSelectCustomer() {
            if (this.selectedCustomer != "" && !this.isCustomerAlreadySelect(this.selectedCustomer.id)) {
                this.$store.dispatch("addNewOrderCustomer", { id: this.selectedCustomer.id, name: this.mapFullName(this.selectedCustomer.firstName, this.selectedCustomer.lastName) });
                this.$store.dispatch("activeOrderCustomer", this.selectedCustomer.id)
                this.$store.dispatch("orderCommit", null);
                this.selectedCustomer = "";
                this.onCloseAddNewOrder()
            }
        },
        // Channel Section
        onChannelClick(channelId) {
            if (this.selectedChannel.id != channelId.id) {
                this.currentChannel = channelId;
                this.modal.channel.show = true
            }
        },
        confirmToSwitchChannel() {
            this.$store.dispatch("posSelectedChannel", this.currentChannel);
            this.$cookies.set("selectedChannel", this.currentChannel);
            this.denyoSwitchChannel();
            window.location.reload();
        },
        denyoSwitchChannel() {
            this.modal.channel.show = false
        },
        onSearchProduct() {
            // if product has been search
            if (this.searchQuery.trim() && this.searchQuery.trim().length > 0) {
                const filterResults = this.posData.product.filter((product) => {
                    if (product.name.toLowerCase().includes(this.searchQuery.toLowerCase().trim())) {
                        // if all category selected
                        if (this.selectedCategoryId == 0) {
                            // filter product with selected channel
                            for (let index = 0; index < product.productPrice.length; index++) {
                                if (product.productPrice[index].priceBook.id == this.selectedChannel.priceBook.id) {
                                    return product;
                                }
                            }
                        }
                        if (this.selectedCategoryId == product.category.id) {
                            for (let index = 0; index < product.productPrice.length; index++) {
                                if (product.productPrice[index].priceBook.id == this.selectedChannel.priceBook.id) {
                                    return product;
                                }
                            }
                        }
                    }
                });
                return this.$store.dispatch("mapProduct", filterResults);
            }
            // if user clear text from search input
            if (this.selectedCategoryId != 0) {
                const filterResults = this.posData.product.filter((product) => {
                    if (product.category.id == this.selectedCategoryId) {
                        for (let index = 0; index < product.productPrice.length; index++) {
                            if (product.productPrice[index].priceBook.id == this.selectedChannel.priceBook.id) {
                                return product;
                            }
                        }
                    }
                });
                return this.$store.dispatch("mapProduct", filterResults);
            }
            return this.$store.dispatch("mapProduct", this.posData.product);
        },
        popup(type) {
            if (type == "profile") {
                this.popupshow.profile = true
                this.ani = false
            } else if (type == 'sidebar') {
                this.popupshow.sidebar = true
            } else {
                this.popupshow.profile = false
                this.popupshow.sidebar = false
                this.ani = false;
            }
        },
        closemodel() {
            this.popupshow.profile = false
            this.popupshow.sidebar = false
            this.ani = true
        },
        menu(item) {
            this.$router.push({ name: item });
            this.popupshow.profile = false
        },
        onLogout() {
            MainService.logout();
        }
    }
}