import Vue from 'vue'

const state = () => ({
  POS: {},
  products: [],
  customers: [],
  customerSelected: [],
  cart: {
    products: []
  },
  activeChannelIndex: 0,
  activeCustomerIndex: 0,
  activeCategoryIndex: 0,
  activeProductGroupIndex: 0
});

const getters = {
  
};

const mutations = {
  SET_ALL_DATA: (state, data) => {
    state.POS = data;
  },
  SET_CUSTOMERS: (state, customers) => {
    state.customers = customers;
  },
  SET_NEW_CUSTOMER: (state, newCustomer) => {
    state.customerSelected.push(newCustomer);
    // set active to customer just added
    state.activeCustomerIndex = state.customerSelected.length-1;
  },
  SET_ACTIVE_CUSTOMER: (state, customerIndex) => {
    state.activeCustomerIndex = customerIndex;
  },
  SET_ACTIVE_CHANNEL: (state, channelIndex) => {
    state.activeChannelIndex = channelIndex;
  },
  SET_ACTIVE_CATEGORY: (state, categoryIndex) => {
    state.activeCategoryIndex = categoryIndex;
    PRODUCT_FILTER(state)
  },
  SET_DEFAULT_CATEGORY: (state, category) => {
    if(state.POS.category){
      state.POS.category.unshift(category);
      PRODUCT_FILTER(state)
    }
  },
  SET_ACTIVE_PRODUCT_GROUP: (state, productGroupIndex) => {
    state.activeProductGroupIndex = productGroupIndex;
    PRODUCT_FILTER(state)
  },
  SET_DEFAULT_PRODUCT_GROUP: (state, productGroup) => {
    if(state.POS.productGroup){
      state.POS.productGroup.unshift(productGroup);
      PRODUCT_FILTER(state)
    }
  },
  ADD_PRODUCT_TO_CART: (state, product) => {
    let customer = state.customerSelected[state.activeCustomerIndex]
    let productIndex = state.cart.products.findIndex(item => item.id == product.id)

    if(productIndex > -1){
      customer.products[productIndex].quantity += 1
      Vue.set(customer.products, productIndex, customer.products[productIndex]);
    }else{
      product.quantity = 1
    }

    if(state.cart.products[0] && productIndex === -1){ customer.products.push(product) }
    if(!state.cart.products[0] && productIndex === -1){ customer.products = [product] }
    
    state.cart.products = customer.products
  },
  CLEAR_ORDER: (state) => {
    state.customerSelected[state.activeCustomerIndex].products = []
    state.activeCustomerIndex = 0
    state.cart.products = []
  }
};

const actions = {
  ADD_CUSTOMER({ commit }, newCustomer) {
    commit("SET_NEW_CUSTOMER", newCustomer);
  }
};

let PRODUCT_FILTER = function(state){
  let category = state.POS.category[state.activeCategoryIndex]
  let productGroup = state.POS.productGroup[state.activeProductGroupIndex]
  
  let channel = state.POS.channel[state.activeChannelIndex]
  console.log("channel ",state.activeChannelIndex)

  let products = state.POS.product

  if(category.id != 0){
    products = products.filter(product => product.category.id == category.id)
  }

  if(productGroup.id != 0){
    console.log("productGroup.id "+productGroup.id)
    products = products.filter(product => product.productGroup.id == productGroup.id)
  }

  products.forEach(function (product) {
    let priceBookIndex = product.productPrice.findIndex(item => item.priceBook.name == channel.channel)
    if(priceBookIndex > -1){
      product.price = product.productPrice[priceBookIndex].price
      product.uom = product.productPrice[priceBookIndex].uom
    }else{
      product.price = 0
    }
  })

  state.products = products
}

export default {
  state,
  getters,
  mutations,
  actions,
};