import axios from 'axios'
import Service from './MainService'
import ApiContant from '../constants/ApiContants'

const OrderService = {}

OrderService.getSaleOrders = async function () {
    return await axios.get(ApiContant.order,
        Service.headers())
        .then((response) => {
            return Service.validateError(response);
        }).catch(function (error) {
            return Service.validateError(error.response);
        })
};

OrderService.createSaleOrder = async function (body) {
    return await axios.post(ApiContant.order, body,
        Service.headers())
        .then((response) => {
            return Service.validateError(response);
        }).catch(function (error) {
            return Service.validateError(error.response);
        })
};

OrderService.updateSaleOrder = async function (id) {
    return await axios.put(ApiContant.order+"/"+id+"/complete", "",
        Service.headers())
        .then((response) => {
            return Service.validateError(response);
        }).catch(function (error) {
            return Service.validateError(error.response);
        })
};

export default OrderService;
