import { mapState } from "vuex";
import ItemProduct from "./components/item-product";
import OrderBill from "./components/order-bill";

export default {
  name: "dashboard",
  data() {
    return {
      activeCustomerIndex: 0,
      modal: {
        customer: false,
      },
      activeProductGroupId: 0,
      removeCustomerId: null,
    };
  },
  created() {
    this.addNewCustomer()
    this.setDefaultProductGroup()
  },
  components: {
    ItemProduct,
    OrderBill,
  },
  computed: {
    ...mapState(["MainStore"])
  },
  mounted() {
    this.$store.commit('PRODUCT_FILTER')
  },
  methods: {
    setDefaultProductGroup(){
      let productGroup = {
        id: 0,
        name: "ទាំងអស់"
      }
      this.$store.commit("SET_DEFAULT_PRODUCT_GROUP", productGroup);
    },
    setActiveCustomer(customerIndex){
      this.$store.commit('SET_ACTIVE_CUSTOMER', customerIndex)
    },
    addNewCustomer(){
      let customer = {
        id: 0,
        name: "Generate"
      }
      this.$store.commit("SET_NEW_CUSTOMER", customer);
    },
    setActiveProductGroupIndex(productGroupIndex){
      this.$store.commit('SET_ACTIVE_PRODUCT_GROUP', productGroupIndex);
    },

    onRemoveCustomer(customerId) {
      // customerId = 0 is a default cannot remove
      if (customerId != 0) {
        this.modal.customer = true;
        this.removeCustomerId = customerId;
      }
    },
    onCloseRemoveCustomer() {
      this.removeCustomerId = null;
      this.modal.customer = false;
    },
    onConfirmRemvoeCustomer() {
      if (this.removeCustomerId == null) return;
      // check if the user have add any product cart -> true -> remove all that order
      if (
        this.orders.find((order) => order.customerId == this.removeCustomerId)
          .lenght > 0
      ) {
        this.orders.find((order, i) => {
          if (order.customerId == this.removeCustomerId) {
            this.orders.splice(i, 1);
          }
        });
      }

      // remove customer object from orderCustomer array
      this.orderCustomer.find((customer, i) => {
        if (customer.id == this.removeCustomerId) {
          const activeOrderCustomerId = this.orderCustomer[i - 1].id;
          this.$store.dispatch("activeOrderCustomer", activeOrderCustomerId);
          this.orderCustomer.splice(i, 1);
        }
      });
      this.onCloseRemoveCustomer();
    },
    onCutomerTabClick(activeOrderCustomerId) {
      this.$store.dispatch("activeOrderCustomer", activeOrderCustomerId);
    },
    // product group section

    onProductGroupClick(productGroupId) {
      this.activeProductGroupId = productGroupId;
      var filterResults = [];
      for (let index = 0; index < this.products.length; index++) {
        let product = this.products[index];

        if (this.selectedCategoryId == 0) {
          if (product.productGroup.id == productGroupId) {
            filterResults.push(product);
          }
        } else if (
          product.category.id == this.selectedCategoryId &&
          product.productGroup.id == productGroupId
        ) {
          filterResults.push(product);
          //   for (let index = 0; index < product.productPrice.length; index++) {
          //     if (
          //       product.productPrice[index].priceBook.id ==
          //       this.selectedChannel.priceBook.id
          //     ) {
          //       filterResults.push(product);
          //     }
          //   }
        }
      }
      this.$store.dispatch("mapProduct", filterResults);
    },
  },
};
