// import imageCompression from 'browser-image-compression';
const Helper = {};

Helper.getFullImage = function (path) {
  return process.env.VUE_APP_BASE_URL + path + "?cache=none";
};

Helper.buildFile = function (blob, name, type) {
  return new File([blob], name, { type: type });
};

Helper.formatPrice = function (price) {
  var formatter = new Intl.NumberFormat("en-US", {
    style: "currency",
    currency: "USD",
    // These options are needed to round to whole numbers if that's what you want.
    //minimumFractionDigits: 0, // (this suffices for whole numbers, but will print 2500.10 as $2,500.1)
    //maximumFractionDigits: 0, // (causes 2500.99 to be printed as $2,501)
  });
  return formatter.format(price);
};
Helper.formatNumber = function (price) {
  var formatter = new Intl.NumberFormat("en-US", {
    minimumFractionDigits: 2, // (this suffices for whole numbers, but will print 2500.10 as $2,500.1)
    maximumFractionDigits: 2, // (causes 2500.99 to be printed as $2,501)
  });
  return formatter.format(price);
};
Helper.numberWithCommas = function (numbers) {
  return numbers?.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

Helper.formatDate = function (input) {
  var datePart = input.match(/\d+/g),
    year = datePart[0], // get only two digits
    month = datePart[1],
    day = datePart[2];
  return day + "-" + month + "-" + year;
};

(Helper.timestampToDate = function (timestamp) {
  const date = new Date(timestamp);
  const day = (date.getDate() < 10 ? "0" : "") + date.getDate();
  const month = (date.getMonth() + 1 < 10 ? "0" : "") + (date.getMonth() + 1);
  const year = date.getFullYear();
  return day + "-" + month + "-" + year;
}),
  (Helper.toCapitalize = function (string) {
    return string.replace(/^\w/, (c) => c.toUpperCase());
  });

Helper.customPagination = function (data) {
  return {
    totalPage: data.totalPage,
    length: data.length,
    page: data.page,
    size: data.size,
  };
};
Helper.mapReponseHelper = function (response) {
  return {
    statusCode: response?.status,
    data: response?.data?.data || response?.data?.message,
    meta: response?.data?.meta || [],
  };
};

export default Helper;
