import { VueTelInput } from 'vue-tel-input'
import UserService from './../../utils/services/UserService'
import AES from './../../utils/AES256'

export default {
    name: "login",
    data() {
        return {
            isShowPassword: false,
            isLogging: false,
            body: {
                user: "", //admin
                password: "" //123456
            }
        }
    },
    components: {
        VueTelInput
    },
    created() {

    },
    mounted() {

    },
    methods: {
        onLogin() {
            let validateMsg = this.validateBody(this.body)
            if(validateMsg === "OK") {
                this.isLogging = true
                let body = {
                    "username": this.body.user,
                    "password": AES.encrypt(this.body.password)
                }
                UserService.login(body).then((response) => {
                    this.isLogging = false
                    if(response.results) {
                        this.$cookies.set('user', response.results)
                        this.$toast.success("Log in successed");
                        location.reload()
                    } else {
                        this.$toast.error(response.response.message);
                    }
                })
            } else {
                this.$toast.error(validateMsg);
            }
        },

        validateBody(body) {
            if(!body.user) { return "Email is required." }
            if(!body.password) { return "Password is required." }
            return "OK"
        }
    }

}