import Vue from "vue";
import VueRouter from "vue-router";

import Login from "./../views/login";
import Dashboard from "./../views/dashboard";
import Invoice from "./../views/invoice";
import SaleOrder from "./../views/sale-order";

const needLogin = (to, from, next) => {
  if (!Vue.$cookies.get('user')) {
      return router.push({ name: 'login' });
  }
  return next();
};

const noNeedLogin = (to, from, next) => {
  if (Vue.$cookies.get('user')) {
      return router.push({ name: 'dashboard' });
  }
  return next();
};

Vue.use(VueRouter);

const routes = [
  { path: "/login", name: "login", component: Login, beforeEnter: noNeedLogin },

  {
    path: "/",
    name: "dashboard",
    component: Dashboard,
    beforeEnter: needLogin,
  },
  {
    path: "/invoice",
    name: "invoice",
    component: Invoice,
    beforeEnter: needLogin,
  },
  {
    path: "/sale-order",
    name: "sale-order",
    component: SaleOrder,
    beforeEnter: needLogin,
  },
];

const router = new VueRouter({
  // mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
