<script>
import Sidebar from "./components/sidebar";
import Navbar from "./components/navbar";
import POSService from "./utils/services/POSService";
import CustomerService from "./utils/services/CustomerService"
import { mapState } from "vuex";


export default {
  name: "app",
  data() {
    return {
      invoiceData: {},
    };
  },
  components: {
    Sidebar,
    Navbar,
  },
  computed: {
    ...mapState(["MainStore"])
  },
  created() {
    this.getAllData()
    this.getCustomer()
  },
  methods: {
    getAllData(){
      POSService.getAllData()
      .then((response) => {
        if(response.results) {
          this.$store.commit("SET_ALL_DATA", response.results);
          let channelId = this.$route.query.channelId

          let channelIndex = response.results.channel.findIndex(item => item.id == parseInt(channelId))
          this.$store.commit("SET_ACTIVE_CHANNEL", channelIndex > -1 ? channelIndex : 0)
        }else{
          this.$toast.error(response.response.message);
        }
      })
    },
    getCustomer(){
      CustomerService.getCustomers()
      .then(response => {
        if(response.results) {
          this.$store.commit("SET_CUSTOMERS", response.results);
        }else{
          console.log(response.response.message);
        }
      })
    }
  },
};
</script>
