import { mapState } from 'vuex'

export default {
    name: 'menusidebar',
    data() {
        return {
            data: {
                user: ""
            },
            display: {
                isActiveMenu: 0,
                menu: "dashboard",
                catalog: true,
            },
            drop: false,
            menudslide: {
                catalog: false,
                info: false,
            },
        }
    },
    created(){
        this.setDefaultCategory()
    },
    computed: {
        ...mapState(["MainStore"]),
    },
    methods: {
        setDefaultCategory(){
            let category = {
              id: 0,
              name: "ទាំងអស់",
              imageUrl: ""
            }
            this.$store.commit("SET_DEFAULT_CATEGORY", category);
        },
        setActiveCategoryIndex(categoryIndex){
            this.$store.commit('SET_ACTIVE_CATEGORY', categoryIndex);
        },


        dorpDown(type) {
            if (type == 'catalog') {
                if (this.menudslide.catalog) {
                    this.menudslide.catalog = false;
                } else {
                    this.menudslide.catalog = true;
                }
            } else {
                if (this.menudslide.info) {
                    this.menudslide.info = false;
                } else {
                    this.menudslide.info = true;
                }
            }
        },
        menuActive() {
            this.data.user = this.$cookies.get('user')
            let menu = this.$root.$route.name;
            if (menu) {
                this.display.menu = menu;
            } else {
                this.display.menu = "dashboard";
            }
        },

        menu(item) {
            this.display.menu = item;
            this.$router.push({ name: item });
        },
        onActiveMenu(menuIndex, categoryId) {
            if (this.display.isActiveMenu != menuIndex) {
                this.display.isActiveMenu = menuIndex
                this.$store.dispatch("selectedCategoryId", categoryId);
                this.filterProductByCategory(categoryId)
            }

        },
        filterProductByCategory(id) {
            // id=0 -> all category
            if (id == 0) {
                this.$store.dispatch("mapProduct", this.posData.product);
                return
            }
            const filterResults = this.posData.product.filter((product) => {
                if (product.category.id == id) {
                    for (let index = 0; index < product.productPrice.length; index++) {
                        if (product.productPrice[index].priceBook.id == this.selectedChannel.priceBook.id) {
                            return product;
                        }
                    }
                }
            });
            this.$store.dispatch("mapProduct", filterResults);
        }
    }
}