// import * as moment from "moment";
import { mapState } from "vuex";
import _debounce from "lodash.debounce";
import orderService from "../../../../utils/services/OrderService";

export default {
  name: "order-bill",
  props: {
    payment: Array,
  },
  data() {
    return {
      activePaymentTypeIndex : 0,
      cashInUSD: 0.00,
      cashInKH: 0.00,
      changeUSD: 0.00,
      changeKH: 0.00,
    };
  },
  computed: {
    debouncedOnCashIn() {
      return _debounce(this.onCashIn, 500);
    },
    ...mapState(["MainStore"])
  },
  created() {

  },
  watch: {
    
  },
  beforeDestroy() {

  },
  methods: {
    getSubTotal(){
      let products = this.MainStore.cart.products;
      let subTotal = 0
      if(products[0]){
        products.forEach(product => {
          subTotal += parseFloat(product.price) * parseInt(product.quantity)
        })
      }
      return subTotal;
    },
    getTotalTax(){
      let subTotal = this.getSubTotal();
      let result = subTotal*parseInt(this.MainStore.POS.tax[0].taxRate)/100
      return result
    },
    getGrandTotalUSD(exchangeRate){
      let subTotal = this.getSubTotal();
      let totalTax = this.getTotalTax()
      return (subTotal + totalTax) * exchangeRate
    },
    onCashIn(){
      let currencyUSDIndex = this.MainStore.POS.currency.findIndex(item => item.symbol == "$")
      let currencyKHIndex = this.MainStore.POS.currency.findIndex(item => item.symbol == "R")
      let currencyUSD = this.MainStore.POS.currency[currencyUSDIndex]
      let currencyKH = this.MainStore.POS.currency[currencyKHIndex]

      let grandTotalUSD = this.getGrandTotalUSD(currencyUSD.exchangeRate)
      let KHToUSD = this.cashInKH/currencyKH.exchangeRate

      this.changeUSD = grandTotalUSD - (parseFloat(this.cashInUSD)+KHToUSD)
      this.changeKH = this.changeUSD * currencyKH.exchangeRate
    },
    onCreateSaleOrder(){
      let currencyUSDIndex = this.MainStore.POS.currency.findIndex(item => item.symbol == "$")
      let currencyKHIndex = this.MainStore.POS.currency.findIndex(item => item.symbol == "R")
      let currencyUSD = this.MainStore.POS.currency[currencyUSDIndex]
      let currencyKH = this.MainStore.POS.currency[currencyKHIndex]

      let products = []
      this.MainStore.cart.products.forEach(product => {
        let newObj = {
          product : {
            id: product.id,
            name: product.name
          },
          discountPercentage: 0,
          discountAmount: 0,
          price: product.price,
          priceAfterDiscount: product.price,
          quantity: product.quantity,
          uom: {
            id: product.uom.id,
            name: product.uom.name
          }
        }
        products.push(newObj)
      })

      let body = {
        "saleOrderDate": new Date(),
        "totalDiscountAmount": 0,
        "totalPrice": this.getSubTotal(),
        "grandTotalPrice": this.getGrandTotalUSD,
        "enabled": true,
        "tax": this.MainStore.POS.tax[0],
        "payment": [
          { 
            "paymentType": this.MainStore.POS.paymentType[this.activePaymentTypeIndex],
            "currency": currencyUSD,
            "amount": parseFloat(this.cashInUSD),
            "changeAmount": parseFloat(this.changeUSD).toFixed(2)
          },
          { 
            "paymentType": this.MainStore.POS.paymentType[this.activePaymentTypeIndex],
            "currency": currencyKH,
            "amount": parseFloat(this.cashInKH),
            "changeAmount": parseFloat(this.changeKH).toFixed(2)
          }
        ],
        "saleOrderDetail": products
      }

      orderService.createSaleOrder(body).then((response) => {
        if(response.results) {
          this.$toast.success("Order successfully");
          this.$store.commit("CLEAR_ORDER")
          this.resetData()
          window.print()
        }else{
          alert("Something went wrong!")
        }
      });
    },
    resetData(){
      this.activePaymentTypeIndex = 0
      this.cashInUSD = 0.00
      this.cashInKH = 0.00
      this.changeUSD = 0.00
      this.changeKH = 0.00
    }
  },
};
