import axios from 'axios'
import Service from './MainService'
import ApiContant from './../constants/ApiContants'

const UserService = {}

UserService.login = async function (body) {
    return await axios.post(ApiContant.login,
        body,
        Service.headerWithoutToken())
        .then((response) => {
            return Service.validateError(response);
        }).catch(function (error) {
            return Service.validateError(error.response);
        })
};

UserService.getUsers = async function (params) {
    return await axios.get(ApiContant.listUser + params,
        Service.headers())
        .then((response) => {
            return Service.validateError(response);
        }).catch(function (error) {
            return Service.validateError(error.response);
        })
};

UserService.getUserDetail = async function (userId) {
    return await axios.get(ApiContant.user + "/" + userId,
        Service.headers())
        .then((response) => {
            return Service.validateError(response);
        }).catch(function (error) {
            return Service.validateError(error.response);
        })
};

UserService.createUser = async function (body) {
    return await axios.post(ApiContant.user,
        body,
        Service.headers())
        .then((response) => {
            return Service.validateError(response);
        }).catch(function (error) {
            return Service.validateError(error.response);
        })
};

UserService.updateUser = async function (body) {
    return await axios.put(ApiContant.updateUser,
        body,
        Service.headers())
        .then((response) => {
            return Service.validateError(response);
        }).catch(function (error) {
            return Service.validateError(error.response);
        })
};

UserService.enableUser = async function (userId, params) {
    return await axios.patch(ApiContant.user + "/" + userId + params,
        "",
        Service.headers())
        .then((response) => {
            return Service.validateError(response);
        }).catch(function (error) {
            return Service.validateError(error.response);
        })
};

UserService.deleteUser = async function (userId) {
    return await axios.delete(ApiContant.user + "/" + userId, {
        data: "",
        headers: Service.headers().headers
    })
        .then((response) => {
            return Service.validateError(response);
        }).catch(function (error) {
            return Service.validateError(error.response);
        })
};

UserService.changePassword = async function (body) {
    return await axios.post(ApiContant.changePassword,
        body,
        Service.headers())
        .then((response) => {
            return Service.validateError(response);
        }).catch(function (error) {
            return Service.validateError(error.response);
        })
};

